import {notification} from "antd";
import axios from "axios";
import {SearchOutlined} from "@ant-design/icons";
import {BASE_URL} from "../api/Url";

export const searchGlobal = (value, paramsPage,variablesSearch) => (dispatch) => {
    if (value == "") {
        window.location.reload();
    } else {
        notification.info({
            message: "Search",
            description: "Mencari data : " + value,
            duration: 1,
            icon: <SearchOutlined/>,
        });
        axios
            .post(
                BASE_URL,
                {
                    processDefinitionId:
                        "getdatajoinwhere:2:d2aed4a7-dff4-11ec-a658-66fc627bf211",
                    returnVariables: true,
                    variables: [
                        {
                            name: "global_join_where",
                            type: "json",
                            value: variablesSearch,
                        },
                        {
                            name: "page",
                            type: "string",
                            value: paramsPage,
                        },
                    ],
                },
                {
                    headers: {
                        "Content-Type": "application/json",
                    },
                }
            )
            .then(function (response) {
                const siswa = JSON.parse(response.data.variables[3].value);
                dispatch({type: "SEARCH_GLOBAL", value: siswa});
            });
    }
};


