import React, {Fragment, useEffect, useRef, useState} from 'react';
import Chart from 'react-apexcharts';
import Adminfooter from '../../components/Adminfooter';
import axios from "axios";
import * as https from "https";
import Navheader from "../../components/Navheader";
import Appheader from "../../components/Appheader";
import {Button, Card, DatePicker, Divider, Modal, notification, PageHeader, Tooltip} from "antd";
import ImgCrop from "antd-img-crop";
import Upload from "antd/es/upload/Upload";
import {PlusOutlined, SearchOutlined, UserAddOutlined} from "@ant-design/icons";
import {BASE_URL} from "../../api/Url";

const iconlList = [{
    name: '350.429', count: 'Siswa', status: 'warning', icon: 'feather-hard-drive',
}, {
    name: '2.356', count: 'Guru', status: 'success', icon: 'feather-box',
}, {
    name: '14', count: 'Kelas Aktif', status: 'info', icon: 'feather-award',
}];

const lineChart = {
    labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec',],
    series: [{
        name: '', data: [35, 16, 34, 36, 18, 35, 26, 34, 26, 18, 36, 18, 36, 34, 36, 18, 16, 18,],
    }, {
        name: '', data: [12, 24, 12, 11, 7, 12, 34, 12, 11, 7, 11, 7, 34, 12, 11, 7, 11, 7],
    },],
    options: {
        chart: {
            height: 100, type: 'bar', // width:'50%',
            stacked: true, toolbar: {
                show: false,
            },
        }, responsive: [{
            breakpoint: 480, options: {
                legend: {
                    position: 'bottom', offsetX: -10, offsetY: 0,
                },
            },
        },], plotOptions: {
            columnWidth: '40%', bar: {
                horizontal: false, borderRadius: 10,
            },
        }, dataLabels: {
            enabled: false,
        }, legend: {
            show: false,
        }, fill: {
            opacity: 1,
        },
    },
};

function BerandaAdmin() {
    const user = localStorage.getItem('user_name');
    const institute = localStorage.getItem('institute');
    const [visible, setVisible] = useState(false);
    const [formPic, setFormPic] = useState(false);

    // const [newInstitute, setNewInstitute] = useState({
    //     name_institute: "sd ceria bahagia",
    //     image_institute:
    //         "",
    //     imagetype_institute: "jpeg",
    //     email_institute: "agung@gmail.com",
    //     tempat_lahir: "jakarta",
    //     phone_institute: "29032932",
    //     state_institute: 11,
    //     city_institute: 1101,
    //     district_institute: 1101010,
    //     subdistrict_institute: 1101010001,
    //     address_institute: "no 7",
    //     fax_institute: 12333,
    //     website_institute: "aneta.id",
    //     email_pic: "tatang@gmail.com",
    //     name_pic: "tatang",
    //     position_pic: "guru admin",
    //     mobile_pic: "232321",
    //     phone_pic: "092323232",
    // });


    useEffect(() => {
        if (institute == 'null') {
            setVisible(true)
            setTimeout(() => {
                notification.info({
                    message: `Informasi`,
                    description:
                        'Sebelum masuk ke dashboard, mohon isi form Institute dan PIC terlebih dahulu.',
                    placement: 'top',
                    duration: 4
                });
            }, 700)

        } else {
            setVisible(false)
        }

    }, []);

    const PicForm = () => {
        return (
            <>
                <h1 className="mt-5">PIC Form</h1>
                <div className="row mt-5">
                    <div className="col-lg-6 mb-3">
                        <div className="form-group">
                            <label className="mont-font fw-600 font-xsss">
                                Name
                            </label>
                            <input
                                name="name_pic"
                                type="text"
                                className="form-control"
                                required
                            />
                        </div>
                    </div>

                    <div className="col-lg-6 mb-3">
                        <div className="form-group">
                            <label className="mont-font fw-600 font-xsss">
                                Phone
                            </label>
                            <input
                                type="number"
                                name="phone_pic"
                                className="form-control"
                                required
                            />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-lg-6 mb-3">
                        <div className="form-group">
                            <label className="mont-font fw-600 font-xsss">
                                Mobile Phone
                            </label>
                            <input
                                type="number"
                                name="mobile_pic"
                                className="form-control"
                                required
                            />
                        </div>
                    </div>
                    <div className="col-lg-6 mb-3">
                        <div className="form-group">
                            <label className="mont-font fw-600 font-xsss">
                                Email
                            </label>
                            <input
                                name="email_pic"
                                type="email"
                                className="form-control"
                                required
                            />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-lg-12 mb-3">
                        <div className="form-group">
                            <label className="mont-font fw-600 font-xsss">
                                Posisi
                            </label>
                            <input
                                name="position_pic"
                                type="text"
                                className="form-control"
                                required
                            />
                        </div>
                    </div>
                </div>

                <div className="text-center">
                    <button
                        className="bg-current border-0 mt-4 text-white font-xsss fw-600 p-3 w175 rounded-lg"
                        type="submit"
                    >
                        Submit
                    </button>
                </div>
            </>
        )
    }

    const ButtonPic = () => {
        return (
            <>
                <Divider>
                    <Tooltip title="Tambahkan PIC">
                        <Button style={{
                            marginTop: 10,
                            width: '50px',
                            height: '50px',
                        }}
                                onClick={() => setFormPic(true)}
                                size='large' type="primary" shape="circle"
                                icon={<UserAddOutlined/>}/>
                    </Tooltip>
                </Divider>
            </>
        )
    }


    const CreateInstitute = (e) => {
        e.preventDefault();
        const userId = localStorage.getItem('user_id')
        const data = {};
        for (const el of e.target.elements) {
            if (el.name !== "") data[el.name] = el.value;
        }

        axios.post(BASE_URL,
            {
                "processDefinitionId": "createinstitute:3:1cbc738a-e713-11ec-9ea6-c6ec5d98c2df",
                "returnVariables": true,
                "variables": [
                    {
                        "name": "user_id",
                        "type": "string",
                        "value": userId
                    },
                    {
                        "name": "m_institute",
                        "type": "json",
                        "value": {
                            "tbl_name": "m_institutesModel",
                            "tbl_coloumn": {
                                "name": data.name_institute,
                                "email": data.email_institute,
                                "phone": data.phone_institute,
                                "fax": data.fax_institute,
                                "address": data.address_intitute,
                                "sub_district_id" : data.subdistrict_institute,
                                "district_id" : data.district_intitute,
                                "city_id" : data.city_institute,
                                "state_id" : data.state_institute,
                                "website" : data.website_institute,
                                "institute_type_id" : data.type_institute,
                                "year_of_found" : data.year_institute
                            }
                        }
                    },
                    {
                        "name": "upload_image",
                        "type": "json",
                        "value": {
                            "image": "",
                            "image_type": "png",
                            "nama_folder": "image_institute"
                        }
                    },
                    {
                        "name": "m_institute_pic",
                        "type": "json",
                        "value": {
                            "tbl_name": "m_institute_pic",
                            "tbl_coloumn": {
                                "name": data.name_pic,
                                "phone": data.phone_pic,
                                "mobile_phone": data.mobile_pic,
                                "email": data.email_pic,
                                "position": data.position_pic
                            }
                        }
                    }
                ]
            },
            {
                headers: {
                    "Content-Type": "application/json",
                },
            }
        )
            .then(function (response) {
                if (response.data.variables[3].value == null){
                    notification.error({
                        message: "Error Create Institute",
                        description: "User Not Found",
                        placement: 'top'
                    })
                }else{
                    localStorage.setItem('institute', response.data.variables[6].value)
                    setVisible(false)
                }
            })
            .catch(function (error) {
                alert(error);
            });

    }

    return (
        <Fragment>
            <div className="main-wrapper">
                <Navheader/>

                <div className="main-content">
                    <Appheader/>

                    <div className="container px-3 py-4">
                        <Modal
                            // title="Please Create Istitute before using Dashboard"
                            centered
                            visible={visible}
                            width={1000}
                            bodyStyle={{overflowY: 'auto', maxHeight: 'calc(100vh - 100px)'}}
                            maskStyle={{backgroundColor: 'rgba(0, 0, 0, 0.85)'}}
                            onOk={() => setVisible(false)}
                            footer={null}
                            closable={false}
                        >
                            <div className="container px-3 py-4">
                                <h1>Institute Form</h1>
                                <div className="row mb-3">
                                    {/*<div className="col-lg-12 mb-5">*/}
                                    {/*    <PageHeader*/}
                                    {/*        className="site-page-header card bg-lightblue text-grey-900 fw-700"*/}
                                    {/*        title="Create Institute"*/}
                                    {/*        style={{ position:'fixed', width: '56rem', zIndex: 1000 }}*/}
                                    {/*    />*/}
                                    {/*</div>*/}
                                    <div className="col-lg-12 mt-5">
                                        <div className="d-flex justify-content-center">
                                            <Card className="bg-lightblue" style={{width: 157}}>
                                                {/*<ImgCrop rotate>*/}
                                                {/*    <Upload*/}
                                                {/*        className="avatar-uploader"*/}
                                                {/*        action="https://www.mocky.io/v2/5cc8019d300000980a055e76"*/}
                                                {/*        listType="picture-card"*/}
                                                {/*        fileList={fileList}*/}
                                                {/*        onChange={onChange}*/}
                                                {/*        onPreview={onPreview}*/}
                                                {/*    >*/}
                                                {/*        {fileList.length < 1 && <PlusOutlined/>}*/}
                                                {/*    </Upload>*/}
                                                {/*</ImgCrop>*/}
                                            </Card>
                                        </div>
                                    </div>
                                </div>
                                <form className="px-3 py-4"
                                      id="institute_form"
                                      onSubmit={CreateInstitute}
                                      method="POST">
                                    <div className="row">
                                        <div className="col-lg-6 mb-3">
                                            <div className="form-group">
                                                <label className="mont-font fw-600 font-xsss">
                                                    Name
                                                </label>
                                                <input
                                                    name="name_institute"
                                                    type="text"
                                                    className="form-control"
                                                    required
                                                />
                                            </div>
                                        </div>

                                        <div className="col-lg-6 mb-3">
                                            <div className="form-group">
                                                <label className="mont-font fw-600 font-xsss">
                                                    Phone
                                                </label>
                                                <input
                                                    type="number"
                                                    name="phone_institute"
                                                    className="form-control"
                                                    required
                                                />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-lg-6 mb-3">
                                            <div className="form-group">
                                                <label className="mont-font fw-600 font-xsss">
                                                    Email
                                                </label>
                                                <input
                                                    name="email_institute"
                                                    type="email"
                                                    className="form-control"
                                                    required
                                                />
                                            </div>
                                        </div>
                                        <div className="col-lg-6 mb-3">
                                            <div className="form-group">
                                                <label className="mont-font fw-600 font-xsss">
                                                    Fax
                                                </label>
                                                <input
                                                    type="number"
                                                    name="fax_institute"
                                                    className="form-control"
                                                    required
                                                />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-lg-6 mb-3">
                                            <div className="form-group">
                                                <label className="mont-font fw-600 font-xsss">
                                                    Provinsi
                                                </label>
                                                <input
                                                    name="state_institute"
                                                    type="text"
                                                    className="form-control"
                                                    required
                                                />
                                            </div>
                                        </div>

                                        <div className="col-lg-6 mb-3">
                                            <div className="form-group">
                                                <label className="mont-font fw-600 font-xsss">
                                                    Kota / Kabupaten
                                                </label>
                                                <input
                                                    type="text"
                                                    name="city_institute"
                                                    className="form-control"
                                                    required
                                                />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-lg-6 mb-3">
                                            <div className="form-group">
                                                <label className="mont-font fw-600 font-xsss">
                                                    Kecamatan
                                                </label>
                                                <input
                                                    type="text"
                                                    name="district_institute"
                                                    className="form-control"
                                                    required
                                                />
                                            </div>
                                        </div>
                                        <div className="col-lg-6 mb-3">
                                            <div className="form-group">
                                                <label className="mont-font fw-600 font-xsss">
                                                    Kelurahan
                                                </label>
                                                <input
                                                    type="text"
                                                    name="subdistrict_institute"
                                                    className="form-control"
                                                    required
                                                />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-lg-12 mb-3">
                                            <label className="mont-font fw-600 font-xsss">
                                                Alamat
                                            </label>
                                            <textarea
                                                className="form-control mb-0 p-3 bg-greylight lh-16"
                                                rows="5"
                                                placeholder="Isi alamat detail..."
                                                name="address_institute"
                                                required
                                            ></textarea>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-lg-6 mb-3">
                                            <div className="form-group">
                                                <label className="mont-font fw-600 font-xsss">
                                                    Website
                                                </label>
                                                <input
                                                    type="text"
                                                    name="website_institute"
                                                    className="form-control"
                                                    required
                                                />
                                            </div>
                                        </div>

                                        <div className="col-lg-6 mb-3">
                                            <div className="form-group">
                                                <label className="mont-font fw-600 font-xsss">
                                                    Tahun Berdiri
                                                </label>
                                                <DatePicker
                                                    className="form-control"
                                                    picker="year"
                                                    placeholder="Pilih Tahun"
                                                    name="year_institute"
                                                    required
                                                />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-lg-12 mb-3">
                                            <label className="mont-font fw-600 font-xsss">
                                                Type
                                            </label>
                                            <select
                                                className="form-control"
                                                name="type_institute"
                                                required
                                            >
                                                <option value="" selected disabled>
                                                    Pilih Kelas
                                                </option>
                                                <option value="1">
                                                    SD
                                                </option>
                                                <option value="2">
                                                    SMP
                                                </option>
                                                <option value="3">
                                                    SMA
                                                </option>

                                            </select>
                                        </div>
                                    </div>

                                    {formPic ? <PicForm/> : <ButtonPic/>}

                                </form>
                            </div>
                        </Modal>
                        <div className="row">
                            <div className="col-lg-12">
                                <div
                                    className="card w-100 bg-lightblue p-lg-5 p-4 mb-5 border-0 rounded-lg d-block float-left">
                                    <h2 className="display1-size display2-md-size d-inline-block float-left mb-0 text-grey-900 fw-700">
                                        Hi, {user}
                                        <span className="font-xssss fw-600 text-grey-600 d-block mb-2 ml-1">
                                                    Selamat datang di Aneta, Semoga Harimu Menyenangkan.
                                                </span>
                                    </h2>
                                    <img
                                        src="data:image/jpeg;base64,"
                                        alt="banner"
                                        className="w250 right-15 top-0 position-absolute d-none d-xl-block"
                                    />
                                </div>
                            </div>
                            <div className="col-lg-12 d-flex mb-4">
                                <h2 className="text-grey-900 ml-1 font-md fw-700">Statistik Sekolah</h2>
                                <select
                                    className="form-select ml-auto float-right border-0 font-xssss fw-600 text-grey-700 bg-transparent"
                                    aria-label="Default select example"
                                >
                                    <option>Sort by latest</option>
                                    <option defaultValue="1">Sort by popularity</option>
                                    <option defaultValue="2">
                                        Sort by price : low to high
                                    </option>
                                    <option defaultValue="3">
                                        Sort by price : high to low
                                    </option>
                                </select>
                            </div>
                        </div>

                        <div className="row">
                            {iconlList.map((value, index) => (<div key={index} className="col-lg-4">
                                <div
                                    className={`card mb-4 border-0 pt-4 pb-4 text-center alert-${value.status} align-items-center rounded-10`}
                                >
                                    <i
                                        className={`psor text-white btn-round-md font-xs ${value.icon} bg-${value.status}`}
                                    ></i>
                                    <h3 className="fw-700 font-xl text-grey-900 mt-2 ls-3 mb-0">
                                        {value.name}
                                    </h3>
                                    <span className="font-xssss ls-0 text-grey-900 fw-700 mt-0">
                          {value.count}
                        </span>
                                </div>
                            </div>))}
                        </div>

                        <div className="row">
                            <div className="col-lg-12">
                                <div className="card border-0 p-4 mt-2 rounded-10">
                                    <div className="card-body d-flex">
                                        <h4 className="font-xss text-grey-800 mb-4 mt-3 fw-700">
                                            Statistik Nilai Siswa
                                        </h4>
                                        <h5 className="ml-auto mr-3 mt-2 text-grey-600 font-xssss fw-700">
                                            <span className="btn-round-xss bg-warning mr-1"></span>
                                            SD
                                        </h5>
                                        <h5 className="mt-2 text-grey-600 font-xssss fw-700">
                                            <span className="btn-round-xss bg-success mr-1"></span>
                                            TK
                                        </h5>
                                    </div>
                                    <Chart
                                        options={lineChart.options}
                                        series={lineChart.series}
                                        type="bar"
                                        width="100%"
                                        height="350"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>

                    <Adminfooter/>
                </div>
            </div>
        </Fragment>);
}

export default BerandaAdmin;
