import React, {Fragment, useEffect} from 'react';

import Adminfooter from '../../components/Adminfooter';
import axios from "axios";
import Chart from "react-apexcharts";
import Navheader from "../../components/Navheader";
import Appheader from "../../components/Appheader";

function BerandaOrangtua() {
    const user = localStorage.getItem('user_name');
    const pieChart = {
        series: [44, 55, 41, 17, 15],
        options: {
            chart: {
                type: 'donut',
            },
            responsive: [
                {
                    breakpoint: 480,
                    options: {
                        chart: {
                            width: 300,
                        },
                        legend: {
                            position: 'bottom',
                        },
                        stacked: true,
                        toolbar: {
                            show: false,
                        },
                    },
                },
            ],
            dataLabels: {
                enabled: false,
            },
            legend: {
                show: false,
            },
        },
    };
    const lineChart = {
        labels: [
            'Jan',
            'Feb',
            'Mar',
            'Apr',
            'May',
            'Jun',
            'Jul',
            'Aug',
            'Sep',
            'Oct',
            'Nov',
            'Dec',
            'Jul',
            'Aug',
            'Sep',
            'Oct',
            'Nov',
            'Dec',
        ],
        series: [
            {
                name: '',
                data: [
                    35, 16, 34, 36, 18, 35, 26, 34, 26, 18, 36, 18, 36, 34, 36, 18, 16, 18,
                ],
            },
            {
                name: '',
                data: [12, 24, 12, 11, 7, 12, 34, 12, 11, 7, 11, 7, 34, 12, 11, 7, 11, 7],
            },
        ],
        options: {
            chart: {
                height: 100,
                type: 'bar',
                // width:'50%',
                stacked: true,
                toolbar: {
                    show: false,
                },
            },
            responsive: [
                {
                    breakpoint: 480,
                    options: {
                        legend: {
                            position: 'bottom',
                            offsetX: -10,
                            offsetY: 0,
                        },
                    },
                },
            ],
            plotOptions: {
                columnWidth: '40%',
                bar: {
                    horizontal: false,
                    borderRadius: 10,
                },
            },
            dataLabels: {
                enabled: false,
            },
            legend: {
                show: false,
            },
            fill: {
                opacity: 1,
            },
        },
    };

    return (
        <Fragment>
            <div className="main-wrapper">
                <Navheader/>
                <div className="main-content">
                    <Appheader/>
                    <div className="container px-3 py-4">
                        <div className="row">
                            <div className="col-lg-12">
                                <div
                                    className="card w-100 bg-lightblue p-lg-5 p-4 mb-5 border-0 rounded-lg d-block float-left">
                                    <h2 className="display1-size display2-md-size d-inline-block float-left mb-0 text-grey-900 fw-700">
                                        Hi, {user}
                                        <span className="font-xssss fw-600 text-grey-600 d-block mb-2 ml-1">
                                                    Selamat datang di Aneta, Semoga Harimu Menyenangkan.
                                                </span>
                                    </h2>
                                    <img
                                        src="data:image/jpeg;base64,"
                                        alt="banner"
                                        className="w250 right-15 top-0 position-absolute d-none d-xl-block"
                                    />
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-lg-12">
                                <div
                                    className="card w-100 bg-lightblue p-lg-5 p-4 mb-4 border-0 rounded-lg d-block float-left"
                                    style={{minHeight: '25vh'}}
                                >
                                    <h2 className="text-center font-weight-bold">Pengumuman</h2>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-lg-8">
                                <div className="card border-0 p-3 mt-4 bg-white rounded-10 shadow-xs">
                                    <div className="card-body d-flex">
                                        <h4 className="font-xss text-grey-800 mb-4 mt-2 fw-700">
                                            Sales Statistics
                                        </h4>
                                        <h5 className="ml-auto mr-3 mt-3 text-grey-600 font-xssss fw-700">
                                            <span className="btn-round-xss bg-warning mr-1"></span>
                                            Sale
                                        </h5>
                                        <h5 className="mt-3 text-grey-600 font-xssss fw-700">
                                            <span className="btn-round-xss bg-success mr-1"></span>
                                            Earn
                                        </h5>
                                    </div>
                                    <Chart
                                        options={lineChart.options}
                                        series={lineChart.series}
                                        type="bar"
                                        width="100%"
                                        height="350"
                                    />
                                </div>
                            </div>
                            <div className="col-lg-4">
                                <div
                                    className="card w-100 p-3 border-0 mt-4 rounded-10 bg-white shadow-xs overflow-hidden">
                                    <div className="card-body d-flex">
                                        <h4 className="font-xss text-grey-800 mt-2 fw-700">
                                            Statistics
                                        </h4>
                                        <h5 className="ml-auto mr-3 mt-2 text-grey-600 mt-3 font-xssss fw-700">
                                            <span className="btn-round-xss bg-warning mr-1"></span>
                                            Sale
                                        </h5>
                                        <h5 className="mt-2 text-grey-600 font-xssss mt-3 fw-700">
                                            <span className="btn-round-xss bg-success mr-1"></span>
                                            Earn
                                        </h5>
                                    </div>
                                    <Chart
                                        options={pieChart.options}
                                        series={pieChart.series}
                                        type="donut"
                                        className="mt-4"
                                    />
                                    <div className="row mt-5">
                                        <div className="col-6 mb-1 text-center mt-1">
                                            <h2 className="font-md mb-1 text-grey-900 fw-700 ls-lg">
                                                44.2k
                                            </h2>
                                            <h4 className="text-grey-500 d-flex justify-content-center fw-600 ls-lg font-xsssss text-uppercase">
                                                    <span
                                                        className="mr-2 bg-facebook btn-round-xss d-inline-block mt-0 me-2 rounded-circle"></span>
                                                this week
                                            </h4>
                                        </div>
                                        <div className="col-6 mb-1 text-center">
                                            <h2 className="font-md mb-1 text-grey-900 fw-700 ls-lg">
                                                54m
                                            </h2>
                                            <h4 className="text-grey-500 d-flex justify-content-center fw-600 ls-lg font-xsssss text-uppercase">
                                                    <span
                                                        className="mr-2 bg-instagram btn-round-xss d-inline-block mt-0 me-2 rounded-circle"></span>
                                                this month
                                            </h4>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <Adminfooter/>
                    </div>
                </div>
            </div>
        </Fragment>);
}

export default BerandaOrangtua;
